  <div class="service">
    <div class="contentBox">
      <line-header text=""></line-header>
      <div class="serviceBox">
        <img-text v-for="item in service"
                  boxW="210px"
                  :src="item.src"
                  imgW="130px"
                  :mytitle="item.title"
