<style scoped rel="stylesheet/less" type="text/less" lang="less">
  .service {
    width: 100%;
    height: 100%;
    background: rgb(241, 241, 241);
  }

  .contentBox {
    width: 1000px;
    margin: auto;
    .serviceBox {
      display: flex;
      justify-content: space-around;
